<template>
    <div class="register-wrapper">
      <mu-row>
        <mu-col width="20" tablet="15" desktop="40"></mu-col>
        <mu-col width="60" tablet="70" desktop="20">
          <h1 class="register">注册</h1>
          <mu-text-field v-model="name" :fullWidth=true label="昵称"/><br/>
          <mu-text-field v-model="email" :fullWidth=true label="邮箱"/><br/>
          <mu-text-field v-model="pass" :fullWidth=true label="密码" type="password"/><br/>
          <mu-text-field v-model="checkPass" :fullWidth=true label="确认密码" type="password"/><br/>
          <mu-flat-button label="注册" @click="register"/>
          </mu-col>
        <mu-col width="20" tablet="15" desktop="40"></mu-col>
      </mu-row>
    </div>
</template>

<script>
import axios from 'axios'

export default {
  name: 'register',
  data () {
    return {
      name: '',
      email: '',
      pass: '',
      checkPass: ''
    }
  },
  methods: {
    close () {
      this.dialog = false
    },
    validName () {
      if (!this.name) {
        alert('昵称不能为空')
        return false
      }
      return true
    },
    validEmail () {
      if (!this.email) {
        alert('邮箱不能为空')
        return false
      } else if (/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/.test(this.email) === false) {
        alert('邮箱格式不正确')
        return false
      }
      return true
    },
    validPass () {
      if (!this.pass || !this.checkPass) {
        alert('密码不能为空')
        return false
      } else if (this.pass !== this.checkPass) {
        alert('两次密码不匹配')
        return false
      }
      return true
    },
    register () {
      var _this = this
      if (this.validName() && this.validEmail() && this.validPass()) {
        axios({
          method: 'POST',
          url: '/api/users',
          headers: {
            'Content-Type': 'application/json'
          },
          data: {
            name: _this.name,
            email: _this.email,
            password: _this.pass
          }
        }).then(function (response) {
          _this.$router.push({name: 'Login'})
        }).catch(function (error) {
          console.log(error)
          alert('邮箱已被注册')
        })
      }
    }
  }
}
</script>

<style>
  .register-wrapper {
    margin-top: 100px;
  }
</style>


